$color_1: #fff;
$color_2: #4a4a4a;
$color_3: #9b9b9b;
$color_4: #797979;
$font_family_1: "sfSemiBold";
$font_family_2: "sfBold";
$background_color_1: #fafbfd;
$background_color_2: #fff;
$background_color_3: #f9f8ee;
$background_color_4: #eef3f9;
$background_color_5: transparent;

.header-profile {
	width: 100%;
	height: 45px;
}
.fixed-bar {
	position: fixed;
	z-index: 99;
	top: 0;
	left: 0;
	right: 0;
}
.bg-grey {
	background: #787878;
}
nav.navbar {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: center;
	height: 45px;
	align-items: center;
}
span.icon-bar {
	position: absolute;
	width: 30px;
	left: 15px;
	height: 30px;
	background: url(../assets/images/profile/icon-bars.png) no-repeat center;
	background-size: auto;
}
.navbar-edit-profile {
	position: absolute;
	right: 15px;
	z-index: 1;
	a {
		color: $color_1;
		text-decoration: none;
	}
	i {
		display: inline-block;
		margin-left: .3rem;
	}
}
.navbar-title-heading {
	position: relative;
	text-transform: uppercase;
	font-weight: 600;
	color: $color_1;
	font-size: 15px;
	font-weight: 600;
	line-height: 22px;
	text-transform: uppercase;
	font-family: $font_family_1;
}
.profile_content {
	position: relative;
	margin-top: 45px;
	width: 100%;
}
.profile-heading {
	position: relative;
	width: 100%;
	height: 96px;
	background-color: $background_color_1;
	padding: 10px 15px;
}
.profile-heading_info {
	position: relative;
	display: flex;
}
.img_user {
	width: 70px;
	height: 70px;
	background: #e9edf1;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}
.profile-info {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-left: 15px;
	width: calc(100% - 70px);
}
.profile_name {
	font-size: 17px;
	font-weight: 600;
	margin-bottom: 10px;
	font-family: $font_family_2;
	letter-spacing: -0.18px;
}
.profile_speciality {
	display: block;
	width: 100%;
	label {
		color: $color_2;
		font-size: 12px;
		display: block;
		margin-bottom: 5px;
	}
	span {
		display: block;
		font-weight: 600;
		font-size: 14px;
		font-family: $font_family_1;
	}
}
.profile_Residency {
	position: relative;
	width: 100%;
	height: 96px;
	background-color: $background_color_2;
	padding: 10px 15px;
	box-shadow: 0 1px 0px #e9edf1;
	z-index: 1;
}
.profile_title {
	color: $color_3;
	display: block;
	margin-bottom: 5px;
	font-size: 12px;
	font-weight: 400;
}
.profile__content {
	display: flex;
	position: relative;
}
.profile__icon {
	width: 48px;
	height: 48px;
	background-color: $background_color_3;
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: url(../assets/images/profile/icon_residency.png);
	background-repeat: no-repeat;
	background-position: center;
}
.profile__content_info {
	display: flex;
	justify-content: center;
	align-content: center;
	padding-left: 15px;
	width: calc(100% - 45px);
	flex-direction: column;
}
.profile_Education {
	position: relative;
	width: 100%;
	height: 96px;
	background-color: $background_color_2;
	padding: 10px 15px;
	box-shadow: 0 1px 0 #e9edf1;
	z-index: 1;
	top: 1px;
}
.education-icon {
	background-color: $background_color_4;
	background-image: url(../assets/images/profile/icon_education.png);
	background-repeat: no-repeat;
	background-position: center;
}
span.not_residency {
	color: $color_3;
	font-size: 14px;
}
.profile_Contact {
	position: relative;
	width: 100%;
	height: 96px;
	background-color: $background_color_2;
	padding: 10px 15px;
	box-shadow: 0 1px 0 #e9edf1;
	.profile_title {
		margin-bottom: 15px;
	}
}
.contact-icon {
	background-image: url(../assets/images/profile/icon_email.png);
	background-repeat: no-repeat;
	background-position: center;
	background-color: $background_color_5;
}
.pre_email_addr {
	color: $color_2;
	font-weight: 600;
	font-size: 14px;
	margin-bottom: 3px;
	line-height: 16px;
	font-family: $font_family_1;
}
.addr-link {
	font-size: 12px;
	color: $color_4;
}
